"use client";

import React, { useEffect, useState } from "react";
import { Button } from "@/components/ui/button";
import { Accordion, AccordionItem, AccordionTrigger, AccordionContent } from "@/components/ui/accordion";
import { Tabs, TabsList, TabsTrigger, TabsContent } from "@/components/ui/tabs";
import { Dialog, DialogTrigger, DialogContent, DialogHeader, DialogTitle, DialogDescription, DialogClose } from "@/components/ui/dialog";
import { Drawer, DrawerTrigger, DrawerContent, DrawerClose } from "@/components/ui/drawer";

export default function HomePage() {
  const [showDialog, setShowDialog] = useState(false);
  const [showDrawer, setShowDrawer] = useState(false);
  const [scrolled, setScrolled] = useState(false);

  useEffect(() => {
    const onScroll = () => {
      setScrolled(window.scrollY > 100);
    };
    window.addEventListener("scroll", onScroll);
    return () => window.removeEventListener("scroll", onScroll);
  }, []);

  return (
    <div className="space-y-20">
      {/* Hero Section */}
      <section className="min-h-screen flex flex-col justify-center items-center text-center">
        <h1
          className={`text-6xl font-extrabold tracking-tight transition-all duration-700 ${
            scrolled ? "opacity-100 translate-y-0" : "opacity-0 -translate-y-12"
          }`}
        >
          Witaj na Zajebistej Stronie
        </h1>
        <p
          className={`mt-6 text-xl max-w-xl transition-all duration-700 delay-200 ${
            scrolled ? "opacity-100 translate-y-0" : "opacity-0 translate-y-12"
          }`}
        >
          Nowoczesna strona z mnóstwem funkcji i animacji.
        </p>
        <div
          className={`mt-10 transition-all duration-700 delay-500 transform ${
            scrolled ? "opacity-100 scale-100" : "opacity-0 scale-90"
          }`}
        >
          <Button onClick={() => setShowDialog(true)} className="px-8 py-4 text-lg">
            Otwórz modal
          </Button>
          <Button onClick={() => setShowDrawer(true)} variant="outline" className="ml-4 px-8 py-4 text-lg">
            Otwórz drawer
          </Button>
        </div>
      </section>

      {/* Features Section */}
      <section>
        <h2 className="text-4xl font-bold mb-8 text-center">Funkcje</h2>
        <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
          <div
            className="p-6 border border-gray-300 rounded-lg shadow hover:shadow-lg transition-shadow transform hover:scale-105"
          >
            <h3 className="text-2xl font-semibold mb-2">Akordeon</h3>
            <Accordion type="single" collapsible>
              <AccordionItem value="item-1">
                <AccordionTrigger>Rozwiń akordeon</AccordionTrigger>
                <AccordionContent>
                  To jest zawartość akordeonu z animacją otwierania i zamykania.
                </AccordionContent>
              </AccordionItem>
            </Accordion>
          </div>
          <div
            className="p-6 border border-gray-300 rounded-lg shadow hover:shadow-lg transition-shadow transform hover:scale-105"
          >
            <h3 className="text-2xl font-semibold mb-2">Zakładki</h3>
            <Tabs defaultValue="tab1">
              <TabsList>
                <TabsTrigger value="tab1">Zakładka 1</TabsTrigger>
                <TabsTrigger value="tab2">Zakładka 2</TabsTrigger>
              </TabsList>
              <TabsContent value="tab1">Zawartość zakładki 1 z animacją.</TabsContent>
              <TabsContent value="tab2">Zawartość zakładki 2 z animacją.</TabsContent>
            </Tabs>
          </div>
          <div
            className="p-6 border border-gray-300 rounded-lg shadow hover:shadow-lg transition-shadow transform hover:scale-105"
          >
            <h3 className="text-2xl font-semibold mb-2">Przyciski</h3>
            <Button className="mr-4">Standardowy</Button>
            <Button variant="outline">Obrysowany</Button>
          </div>
        </div>
      </section>

      {/* Dialog Section */}
      <Dialog open={showDialog} onOpenChange={setShowDialog}>
        <DialogContent>
          <DialogHeader>
            <DialogTitle>Modal z animacją</DialogTitle>
            <DialogDescription>
              To jest modal z animacją otwierania i zamykania.
            </DialogDescription>
          </DialogHeader>
          <div className="mt-4">
            <Button onClick={() => setShowDialog(false)}>Zamknij</Button>
          </div>
        </DialogContent>
      </Dialog>

      {/* Drawer Section */}
      <Drawer open={showDrawer} onOpenChange={setShowDrawer}>
        <DrawerContent>
          <div className="p-6">
            <h2 className="text-2xl font-bold mb-4">Drawer z animacją</h2>
            <p>To jest zawartość drawer z animacją przesuwania.</p>
            <Button onClick={() => setShowDrawer(false)} className="mt-6">
              Zamknij
            </Button>
          </div>
        </DrawerContent>
      </Drawer>
    </div>
  );
}
